import { useEffect } from 'react';
import styled from 'styled-components';

export const ModalContainer = styled.div`
  // Modal    CSS 
  display : flex;
  justify-content : center;
  align-items : center;
  height : 100%;
`;

export const ModalBackdrop = styled.div`
  // Modal     CSS 
  z-index: 1; // 
  position: fixed;
  display : flex;
  justify-content : center;
  align-items : center;
  background-color: rgba(0,0,0,0.4);
  border-radius: 10px;
  top : 0;
  left : 0;
  right : 0;
  bottom : 0;
`;

export const ModalBtn = styled.button`
  background-color: var(--coz-purple-600);
  text-decoration: none;
  border: none;
  padding: 20px;
  color: black;
  border-radius: 30px;
  cursor: grab;
`;

export const ExitBtn = styled(ModalBtn) `
    background-color : #4000c7;
    border-radius: 10px;
    text-decoration: none;
    margin: 10px;
    padding: 5px 10px;
    width: 40px;
    height: 40px;
    display : flex;
    justify-content : center;
    align-items : center;
`;


export const ModalView = styled.div.attrs((props) => ({
    // attrs     div     .
    role: 'dialog',
    }))`
    // Modal CSS .
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
    width: 500px;
    heigth: 200px;
    background-color: #ffffff;
    font-family: 'SansM';
      >div.desc {
        margin: 50px;
        font-size: 20px;
        color: var(--coz-purple-600);
      }
  `;

export const ModalButton = styled.button`
    background-color: #4CAF50; //  
    color: white;
    padding: 10px 15px;
    margin: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    &:hover {
      background-color: #45a049;
    }
  `;

export const UserLoginModal = ({ 
    content, 
    isOpen, setIsOpen,        //    
    closeMethod
}) => {

    //      props 
  useEffect(() => {
      setIsOpen(isOpen); //      
  }, [isOpen, setIsOpen]);

    return (
        <>
            {isOpen && 
                <ModalContainer>
                    <ModalBackdrop onClick={closeMethod}>
                        <ModalView onClick={(e) => e.stopPropagation()}>
                            <div className='desc'>{content}</div>
                            <div>
                                <ModalButton onClick={closeMethod}></ModalButton>
                            </div>
                        </ModalView>
                    </ModalBackdrop>
                </ModalContainer>
            }
        </>
    );
};

export default UserLoginModal;
