import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import UserLoginModal from '../components/UserLoginModal';

const UserSignup = () => {
  const navigate = useNavigate();
  let [name, setName] = useState('');
  let [sex, setSex] = useState(true);
  let [nickname, setNickname] = useState('');
  let [year, setYear] = useState('');
  let [month, setMonth] = useState('');
  let [day, setDay] = useState('');
  let [email, setEmail] = useState('');
  let [pwd, setPwd] = useState('');
  let [pwdconfirm, setPwdconfirm] = useState(''); //   
  let [showModal, setShowModal] = useState(false);
  let [modalMessage, setModalMessage] = useState('');
  let [errorMessage, setErrorMessage] = useState('');

  const checkPassword = () => {
    if (pwd !== pwdconfirm) {
      setErrorMessage('  .  .');
      return false;
    } else {
      setErrorMessage('');
      return true;
    }
  };

  const onSubmit = async () => {
    if (!checkPassword()) return;

    const formatYear = year.replace('', '');
    const formatMonth = month.replace('', '').padStart(2, '0');
    const formatDay = day.replace('', '').padStart(2, '0');

    // year, month, day 'YYYY-MM-DD'   
    const birthdate = `${formatYear}-${formatMonth}-${formatDay}`;

    const userData = {
      username: name,
      gender: sex,
      nickname: nickname,
      birth: birthdate,
      email: email,
      password: pwd,
    };

    try {
      const response = await axios.post('/api/user', userData, {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (response.status === 201) {
        console.log('User registered successfully');
        navigate('/user/login');
      }
    } catch (error) {
      const message =
        error.response?.data?.message ||
        ' .   .';
      setModalMessage(message);
      setShowModal(true);
    }
  };

  const goBack = () => {
    navigate('/user/login');
  };

  //   : ,,
  const BIRTHDAY_YEAR_LIST = Array.from(
    { length: 25 },
    (_, i) => `${i + 1990}`
  );
  const BIRTHDAY_MONTH_LIST = Array.from(
    { length: 12 },
    (_, i) => `${i + 1}`
  );
  const BIRTHDAY_DAY_LIST = Array.from({ length: 31 }, (_, i) => `${i + 1}`);
  const SEX_LIST = ['', ''];

  const handleSexChange = (e) => {
    const selectedSex = e.target.value;
    setSex(selectedSex === '');
  };

  const calculateWidthSize = (originalSize, ratio) => {
    return Math.round(window.innerWidth * ratio) || originalSize;
  };

  const calculateHeightSize = (originalSize, ratio) => {
    return Math.round(window.innerHeight * ratio) || originalSize;
  };

  return (
    <div style={{ padding: '140px' }}>
      <UserLoginModal
        content={modalMessage}
        isOpen={showModal}
        setIsOpen={setShowModal}
        closeMethod={() => setShowModal(false)}
      />
      <div
        className="container d-flex justify-content-center vh-50"
        style={{
          width: calculateWidthSize(500, 0.4),
          height: calculateHeightSize(100, 0.3),
        }}
      >
        <div className="d-flex flex-column align-items-center">
          <h1 style={{ fontFamily: 'SansM', fontSize: '35px' }}></h1>
          <div style={{ textAlign: 'center', paddingTop: '40px' }}>
            <div className="emailFrame" style={{ marginTop: '20px' }}>
              <input
                className="form-login"
                style={{
                  fontFamily: 'SansM',
                  padding: '15px',
                  fontSize: '20px',
                  borderRadius: '5px',
                  border: '1px solid #ccc',
                }}
                type="email"
                placeholder=" "
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                required
              />
            </div>
            <div className="pwdFrame" style={{ marginTop: '20px' }}>
              <input
                className="form-login"
                style={{
                  fontFamily: 'SansM',
                  padding: '15px',
                  fontSize: '20px',
                  borderRadius: '5px',
                  border: '1px solid #ccc',
                }}
                type="password"
                placeholder=" "
                value={pwd}
                onChange={(e) => {
                  setPwd(e.target.value);
                }}
                required
              />
            </div>
            <div className="pwdConfirmFrame" style={{ marginTop: '20px' }}>
              <input
                className="form-login"
                style={{
                  fontFamily: 'SansM',
                  padding: '15px',
                  fontSize: '20px',
                  borderRadius: '5px',
                  border: '1px solid #ccc',
                }}
                type="password"
                placeholder=" "
                value={pwdconfirm}
                onChange={(e) => {
                  setPwdconfirm(e.target.value);
                }}
                onBlur={checkPassword} //      
                required
              />
            </div>
            <div className="nameFrame-sexFrame" style={{ marginTop: '20px' }}>
              <input
                className="form-login"
                style={{
                  fontFamily: 'SansM',
                  padding: '15px',
                  fontSize: '20px',
                  borderRadius: '5px',
                  border: '1px solid #ccc',
                  width: '300px',
                  marginRight: '40px',
                }}
                type="name"
                placeholder=""
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                required
              />
              <select
                className="sexBox"
                style={{
                  fontFamily: 'SansM',
                  padding: '15px',
                  fontSize: '20px',
                  borderRadius: '5px',
                  border: '1px solid #ccc',
                }}
                value={sex ? '' : ''}
                onChange={handleSexChange}
                required
              >
                <option value="" disabled>
                  
                </option>
                {SEX_LIST.map((sex, index) => (
                  <option key={index}>{sex}</option>
                ))}
              </select>
            </div>
            <div className="nicknameFrame" style={{ marginTop: '20px' }}>
              <input
                className="nicknameBox"
                style={{
                  fontFamily: 'SansM',
                  padding: '15px',
                  fontSize: '20px',
                  borderRadius: '5px',
                  border: '1px solid #ccc',
                }}
                type="nickname"
                placeholder=""
                value={nickname}
                onChange={(e) => {
                  setNickname(e.target.value);
                }}
                required
              />
            </div>
            <div className="birthdateFrame" style={{ marginTop: '20px' }}>
              <div className="birthdateSelectFrame">
                <label
                  className="form-label"
                  style={{
                    fontFamily: 'SansM',
                    fontSize: '20px',
                    marginRight: '10px',
                  }}
                >
                  
                </label>
                <select
                  className="birthdateBox yearBox"
                  style={{
                    fontFamily: 'SansM',
                    padding: '15px',
                    fontSize: '20px',
                    borderRadius: '5px',
                    border: '1px solid #ccc',
                  }}
                  type="year"
                  value={year}
                  onChange={(e) => {
                    setYear(e.target.value.replace('', ''));
                  }}
                  required
                >
                  {BIRTHDAY_YEAR_LIST.map((year, index) => (
                    <option key={index}>{year}</option>
                  ))}
                </select>
                <select
                  className="birthdateBox monthBox"
                  style={{
                    fontFamily: 'SansM',
                    padding: '15px',
                    fontSize: '20px',
                    borderRadius: '5px',
                    border: '1px solid #ccc',
                  }}
                  type="month"
                  value={month}
                  onChange={(e) => {
                    setMonth(e.target.value.replace('', ''));
                  }}
                  required
                >
                  {BIRTHDAY_MONTH_LIST.map((year, index) => (
                    <option key={index}>{year}</option>
                  ))}
                </select>
                <select
                  className="birthdateBox dayBox"
                  style={{
                    fontFamily: 'SansM',
                    padding: '15px',
                    fontSize: '20px',
                    borderRadius: '5px',
                    border: '1px solid #ccc',
                  }}
                  type="day"
                  value={day}
                  onChange={(e) => {
                    setDay(e.target.value.replace('', ''));
                  }}
                  required
                >
                  {BIRTHDAY_DAY_LIST.map((day, index) => (
                    <option key={index}>{day}</option>
                  ))}
                </select>
              </div>
            </div>
          </div>

          <div>
            <div className="btn" style={{ marginTop: '40px' }}>
              <Link to="/user/login" style={{ textDecoration: 'none' }}>
                <button
                  type="button"
                  onClick={onSubmit}
                  style={{ background: 'transparent', border: 'none' }}
                >
                  <img
                    src={require('../img/CheckBtn.png')}
                    alt=""
                    className="btn-image"
                  />
                </button>
              </Link>
            </div>
          </div>
          <div>
            <div className="btn" style={{ marginTop: '20px' }}>
              <Link to="/user/login" style={{ textDecoration: 'none' }}>
                <button
                  type="button"
                  onClick={goBack}
                  style={{ background: 'transparent', border: 'none' }}
                >
                  <img
                    src={require('../img/BacktoBtn.png')}
                    alt=""
                    className="btn-image"
                  />
                </button>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default UserSignup;
